// $cardHeight: calc(50vh - 50px);

body{
    min-width: 1230px;
    min-height: 800px;
}
.header{
    height:60px;
    background: #1f2e86;
}
.nav {
    margin-bottom: 30px;
}
.highcharts-credits{
    display: none;
}
.cardWrapper {
    width: 100%;
    height: 480px;
    color: $textcolor;
    margin-bottom:50px;
    &-graph {
        height:auto;
        margin-bottom:20px;
    }
}
.card {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background-color: $white;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
}
.card-script{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background-color: $white;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
    justify-content: space-between;
}
.scroll::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: transparent;
}

.scroll::-webkit-scrollbar {
    width: 4px;
    background-color: transparent;
}

.scroll::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: rgb(31, 46, 134);
}
